@use 'base/mixins' as m;
@use 'base/variables' as v;

.swiper-beneficios {
    height: 45rem;
    max-width: 30rem;

    @include m.tablet {
        max-width: none;
        height: 100%;
    }

    &__wrapper {
        align-items: center;

        @include m.tablet {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 3rem 2rem;
            margin-top: 5rem;
        }

        @include m.desktop {
            grid-template-columns: repeat(3, 1fr);
        }
    }

    &__slide {
        display: flex !important;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 2rem;
        
        @include m.tablet {
            justify-content: flex-start;
        }
    }

    &__pagination {
        & .swiper-pagination-bullet-active {
            background: v.$purple_400;
        }
    }
}

.beneficio {
    background-color: v.$white;
    height: 100%;

    @include m.tablet {
        -webkit-box-shadow: 0px 5px 10px 4px rgba(252,162,231,0.6);
        -moz-box-shadow: 0px 5px 10px 4px rgba(252,162,231,0.6);
        box-shadow: 0px 5px 10px 4px rgba(252,162,231,0.6);
        padding: 2rem;
        border-radius: 1.5rem;

        &:last-child {
            grid-column: 1/3;
        }
    }

    @include m.desktop {
        &:last-child {
            grid-column: 1/4;
        }
    }

    &__contenedor-texto {
        max-width: 95%;
        text-align: center;
    }

    &__descripcion {
        margin: 1rem 0;
    }
}

.contenedor-icono{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    position: relative;
    text-align: center;
    padding: 2rem;
    color: v.$white;

    &::before {
        content: '';
        position: absolute;
        height: 10rem;
        width: 10rem;
        left: 50%;
        z-index: -1;
        clip-path: circle(50%);
        top: 50%;
        transform: translate(-50%, -50%);
        background: linear-gradient(90deg, v.$purple_200 50%, lighten(v.$purple_200, 10) 50%); 
        border: 5px solid darken(v.$purple_200, 5);
        border-radius: 50%;
    }

    i {
        font-size: 5rem;
    }

    @include m.tablet {
        &::before {
            z-index: 0;
        }

        i {
            z-index: 1;
        }
    }
}

    


