@use 'base/variables' as v;
@use 'base/mixins' as m;

.llamado-contacto-contenedor{
    @include m.tablet {
        display: grid;
        grid-template-columns: 50% 50%;
    }
}

.llamado-accion {
    @include m.bg-with-overlay("llamadoAccion", v.$black, .8, ".llamado-accion");
    height: 25rem;

    @include m.tablet {
        height: 100%;
    }

    &__texto {
        margin: 0;
        padding: 2rem 0;
        color: v.$white;
        text-align: center;
        font-size: 2rem;

        span {
            display: block;
            margin-top: 2rem;
            padding: 1rem;
            background-color: v.$purple_300;
            border-radius: 1.5rem;
            font-weight: v.$bold;
            font-size: 2.2rem;
        }

        @include m.desktop {
            font-size: 3rem;

            span {
                font-size: 3.2rem;
            }
        }
    }
}

.datos-contacto {
    &__lista {
        margin-top: 3rem;

        @include m.telefono {
            display: grid;
            grid-template-columns: 50% 50%;
        }

        @include m.tablet {
            display: block;
        }
    }

    &__item {
        margin-bottom: 2rem;
        display: flex;
        align-items: center;
        gap: 1rem;

        p {
            font-size: 1.5rem;
            margin: 0;
        }

        .enlace-email {
            color: v.$purple_400;
        }

        @include m.telefono {
            flex-direction: column;
            gap: 2rem;

            p{
                font-size: 1.8rem;
            }

            &:last-child{
                margin-top: 2rem;
                grid-column: 1 / 3;
            }
        }

        @include m.tablet {
            flex-direction: row;

            &:last-child {
                margin-top: 0;
            }
        }

        @include m.desktop {
            p {
                font-size: 2rem;
            }
        }
    }
    &__contenedor-icono {
        background-color: v.$purple_300;
        padding: 1rem;
        border-radius: 1rem;
        
        i {
            color: v.$white;
            font-size: 2rem;
            width: 2rem;
            height: 2rem;
            display: flex;
            justify-content: center;
        }

        @include m.telefono {
            i {
                font-size: 3rem;
                width: 3rem;
                height: 3rem;
            }
        }
    }
}